.container {
  display: flex; /* Flexbox 레이아웃 사용 /
  justify-content: space-between; / 요소들을 좌측, 중앙, 우측으로 분배 */
  
}

.full{
  background-image: url("../image/skuniv.jpg");
  background-repeat: no-repeat;
  /*background-attachment: fixed;*/
  background-size: cover;
  opacity: 0.5;     /*투명도*/
  height:100vh;
  width:100vw;
}

/* 각 세로로 3등분되는 영역 */
#column {
    flex-basis: 30%; /* 각 컬럼의 너비를 30%로 설정 */
    padding: 10px; /* 간격을 추가할 수 있습니다. */
    border: 1px solid #ccc; /* 테두리 추가 (선택 사항) */
}

.symbol{
  background-image: url("../image/ssportal.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

/* 각 세로로 3등분되는 영역 */
#column2 {
  flex-basis: 30%; /* 각 컬럼의 너비를 30%로 설정 */
  padding: 10px; /* 간격을 추가할 수 있습니다. */
  border: 1px solid #ccc; /* 테두리 추가 (선택 사항) */
}

footer{
  text-align:center;
  color:darkred;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
